.main {
    margin-top: 15px;
    width: 100%;
    border: 2px solid rgb(255, 255, 255);
    border-radius: 25px;
    background: #22222244;
    padding: 10px 20px;
}

.left {
    width: 460px;

    @media screen and (max-width: 1573px) {
        width: 100%;
    }
}

.right {
    width: 460px;
    margin-left: 10px;

    @media screen and (max-width: 1573px) {
        width: 100%;
    }
}

.nav-exit svg {
    margin-right: 3px;
}

.left-content-nav {
    width: 97%;
}

.left-nav-btns {
    width: 50%;
}

.left-nav-btns a {
    font-size: 14px;
    padding: 3px 10px;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
}

.left-nav-btns a:first-child {
    border-left: 1px solid white;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.left-nav-btns a:last-child {
    border-right: 1px solid white;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.content {
    margin-top: 10px;
    background-color: rgba(0, 0, 0, 0.3);
    border: 2px solid rgb(255, 255, 255);
    border-radius: 20px;
    padding: 10px;
}

.inputdiv {
    background: #22222244;
    border: 1px solid white;
    border-radius: 10px;
    padding-left: 3px;
}

.inputdiv input {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    border: none;
}


.left-content-nav-right {
    font-size: 13px;
}

.contentcontainer {
    height: 430px;
    padding-right: 3px;
    overflow-y: scroll;
}

.contentcontainer {
    &::-webkit-scrollbar {
        width: 15px;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #eeeeee;
        border-radius: 10px;
        border: 3px solid rgb(49, 49, 49);
    }

    &::-webkit-scrollbar-track {
        background-color: rgb(49, 49, 49);
        border-radius: 10px;
        padding: 5px;
    }
}

.content-item {
    background: rgba(217, 217, 217, 0.2);
    border: 1px solid #ffffff;
    border-radius: 7.5px;
    height: 28px;
    margin-top: 3px;
    padding-left: 3px;
}

.content-item p {
    overflow: hidden;
    text-overflow: ellipsis;
}
.tasks {
    width: 18%;
}

.proc {
    width: 17%;
}

.time {
    width: 15%;
}

// RIGHT

.right-nav p {
    margin-left: 5px;
}

.right-nav {
    height: 29px;
}