.tasks {
    font-family: "comforta";
    width: 100%;
    height: 100vh;
    color: white;
    display: flex;
    align-items: flex-start;
}
.tasks__top {
    margin-top: 8px;
}
.tasks-background {
    width: 100%;
    height: 100vh;
    z-index: -1;
    position: fixed;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.4;
}

.tasksblur {
    width: 100%;
    height: 100vh;
    padding: 0px 300px;
    @media (max-width: 1573px) {
        padding: 0px 100px;
    }
    @media (max-width: 1000px) {
        padding: 0px 50px;
    }
    @media (max-width: 850px) {
        padding: 0px 10px;
    }
}

.tasks__top {
    display: flex;
    justify-content: space-between;
    width: 100%;
